<template>
  <div class="send-page">

    <v-container>
      <v-row class="d-flex date-and-pref">
        <v-col xl="12">
          <v-select
              :items="items"
              label="Сервис-центр"
              v-model="title"
          ></v-select>
        </v-col>

      </v-row>
    </v-container>

    <v-container>
      <v-row>
        <v-col xs="7">
          <input type="datetime-local" name="date" id="date" maxlength="250px" v-model="dateValue">
        </v-col>

        <v-col md="6" sm="6" xs="6">
          <v-btn-toggle
              v-model="currentModel"
              tile
              color="blue darken-4"
              group
          >
            <v-btn v-for="(model, idx) in models" :key="idx" :value="model">
              {{ model }}
            </v-btn>

          </v-btn-toggle>
        </v-col>
      </v-row>
    </v-container>


    <div class="col-sm-12">


      <div class="d-flex col-sm-12 col-12">

      </div>


    </div>


    <div class="col-sm-12 col-12">
      <v-sheet
          class="v-sheet-textarea-send"
          elevation="9"
      >

        <v-textarea
            v-model="inputText"
            class="textarea-send"
            clearable
            clear-icon="mdi-close-circle"
            height="350px"
            rows="15"
        ></v-textarea>

      </v-sheet>
    </div>

    <v-tooltip bottom>
      <template v-slot:activator="{ on, attrs }">
        <div class="col-sm-12 col-12">
          <v-btn
              class="btn-send"
              block
              elevation="8"
              large
              v-bind="attrs"
              v-on="on"
              @click.prevent="sendItemsToSpreadsheet"
          >
            Отправить данные
          </v-btn>
        </div>
      </template>
      <span>Перед отправкой убедитесь в корректности данных</span>
    </v-tooltip>

    <!--    SNACKBAR-->

    <v-snackbar
        v-model="isSnackbarActive"
        :timeout="snackbarTimeout"
        top
        right
        color="blue darken-4"
        rounded
    >
      {{ snackBarText }}

      <template v-slot:action="{ attrs }">
        <v-icon
            v-bind="attrs"
            @click="isSnackbarActive = false"
        >mdi-close-circle
        </v-icon>
      </template>
    </v-snackbar>

    <v-snackbar
        v-model="isRedSnackbarActive"
        :timeout="redSnackbarTimeout"
        top
        right
        color="red darken-3"
    >
      {{ redSnackBarText }}

      <template v-slot:action="{ attrs }">
        <v-icon
            v-bind="attrs"
            @click="isRedSnackbarActive = false"
        >mdi-close-circle
        </v-icon>
      </template>
    </v-snackbar>


  </div>
</template>

<script>
import { dateForDateTimeInputValue } from '../utils/dateTime'
import axios from 'axios'
import cfg from '../config'

export default {
  name: 'SendView',
  data: () => {
    return {
      dateValue: null,
      inputText: '',
      selected: {},

      items: null,
      title: '',
      itemsData: [],
      models: [],
      currentModel: undefined,

      isSnackbarActive: false,
      isRedSnackbarActive: false,
      snackbarTimeout: 2000,
      redSnackbarTimeout: 4000,
      snackBarText: 'Информация успешно отправлена',
      redSnackBarText: 'Информация успешно отправлена',

      toggle_exclusive: undefined,
    }
  },
  async beforeMount() {
    this.dateValue = dateForDateTimeInputValue(new Date())

    const { data } = await axios.get(`${cfg.apiUri}/info`)
    this.items = data.serviceCentreList.map(item => item.title)
    this.itemsData = data.serviceCentreList
    this.models = data.modelsList.sort((a, b) => a > b ? 1 : -1)
  },
  computed: {
    codesArray: function () {
      if (this.inputText === '') return []
      return this.inputText.split(`\n`)
    },
    selectedObj: function () {
      if (this.title === '') return {}
      return this.itemsData.filter(item => item.title === this.title)[0]
    }
  },
  methods: {

    async sendItemsToSpreadsheet() {

      let errors = []
      if (this.title === '') errors.push('Не выбран сервисный центр.')
      if (this.currentModel === undefined) errors.push('Не выбрана модель.')
      if (this.codesArray.length === 0) errors.push('Список серийных номеров пуст.')

      if (errors.length) {
        this.redSnackBarText = errors.map((error, id) => `${id + 1} — ${error}`).join(' ')
        this.isRedSnackbarActive = true
        return
      }

      const spreadsheetId = this.selectedObj.spreadsheetId
      const currentDate = this.timeStampToExcelDate(+new Date(this.dateValue))
      const model = this.currentModel
      const data = this.codesArray.map(code => [model, code, currentDate])

      await axios.post(`${cfg.apiUri}/write/${spreadsheetId}/send`, data)

      // clear
      this.dateValue = dateForDateTimeInputValue(new Date())
      this.title = ''
      this.inputText = ''
      this.currentModel = undefined

      this.isSnackbarActive = true
    },

    timeStampToExcelDate: function (timeStamp) {
      return timeStamp / (1000 * 60 * 60 * 24) + 25569 + 3 / 24
    }
  }
}
</script>

<style>

.send-page {
  /*display: flex;*/
  /*align-items: center;*/
  /*flex-wrap: wrap;*/

}

#date:focus-visible {
  outline-style: none;
}

input[type="datetime-local"] {
  height: 32px;
  text-align: left;
  color: #424242;
  font-size: 16px;

}

input[type="datetime-local"]::-webkit-calendar-picker-indicator {
  margin-left: 0;
  padding: 0;

}

input[type="datetime-local"]::-webkit-date-and-time-value {

}

.v-sheet-textarea-send {
  min-height: 350px;
  height: auto;
  border-radius: 25px;
  padding-top: 35px;
  padding-left: 15px;
}

.textarea-send {
  border-radius: 20px;
  position: relative;
  padding: 15px;
  font-size: 14px;
}

.btn-send {
  border-radius: 12px;
  background-color: #0075FF !important;
  color: aliceblue !important;
  font-weight: bolder;
  font-size: 1rem;
}

.row {
  align-items: center !important;
}


</style>
